<template>
  <div class="make-order">
    <div class="container">
    	<h3 class="title">代客下单</h3>
    	<div class="memebr-address">
    		<p>请添加收货地址</p>

    		<span class="add">新增</span>
    	</div>

    	<div class="distribution-time">
    		<span>配送时间</span>
    		<div @click="selectTime">
    			{{selectDistributionTime}}
    			<i class="cubeic-arrow"></i>
    		</div>
    	</div>

    	<!-- S 搜索 -->
	    <div class="search">
	     	<cube-input
			:placeholder="placeholder"
			 :clearable="clearable"
	     	>选择</cube-input>
	     	<cube-button type="submit">搜索</cube-button>
	    </div>
	    <!-- E 搜索 -->

	    <div class="book-list">
	    	<table>
	    		<thead>
	    			<tr>
		    			<th>选择</th>
		    			<th>ID</th>
		    			<th>图书名称</th>
		    			<th>库存</th>
		    		</tr>
	    		</thead>
				
				<tbody>
					<tr>
						<td>
							<div class="text">
								<cube-checkbox
									v-model="checked1"
									shape="square"
								>
									<span></span>
								</cube-checkbox>
							</div>
						</td>
						<td><div class="text">10001</div></td>
						<td>
							<div class="text book">
								黑猫警长
								<div class="label">
									<cube-button>2-4岁</cube-button>
									<cube-button>3-6岁</cube-button>
								</div>
							</div>
						</td>
						<td><div class="text">2</div></td>
					</tr>
					<tr>
						<td>
							<div class="text">
								<cube-checkbox
									v-model="checked2"
									shape="square"
								>
									<span></span>
								</cube-checkbox>
							</div>
						</td>
						<td><div class="text">10002</div></td>
						<td>
							<div class="text book">
								小猪佩琪
								<div class="label">
									<cube-button>2-4岁</cube-button>
								</div>
							</div>
						</td>
						<td><div class="text">1</div></td>
					</tr>
				</tbody>
	    	</table>

	    	<cube-button class="add">添加</cube-button>
	    </div>

	    <cube-button class="submit-order" @click="submitOrder">提交订单</cube-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    	timePicker: null,
    	distributionTimes: [{
    		text: '工作日',
    		value: '工作日'
    	}, {
    		text: '周末',
    		value: '周末'
    	}, {
    		text: '工作日及周末',
    		value: '工作日及周末'
    	}],
    	selectDistributionTime: '工作日及周末',
    	placeholder: '请输入图书id/名称/标签',
    	clearable: {
	        visible: true,
	        blurHidden: true
	    },
	    checked1: false,
	    checked2: true
    }
  },
  props: {
  },
  methods: {
  	selectTime() {
  		if (!this.timePicker) {
	  		this.timePicker = this.$createPicker({
	            title: '配送时间',
	            data: [this.distributionTimes],
	            selectedIndex: [2],
	            onSelect: this.selectHandle,
	            onCancel: this.cancelHandle
	        }).show()
	  	} else {
	  		this.timePicker.show()
	  	}
  	},
  	selectHandle(selectedVal, selectedIndex, selectedText) {
  		this.selectDistributionTime = selectedVal[0]
  	},
  	cancelHandle() {
	},
	submitOrder() {
		this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: '订单操作',
        content: '是否确定提交此订单',
        confirmBtn: {
          text: '确定按钮',
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: '取消按钮',
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: () => {
          this.$createToast({
            type: 'correct',
            time: 1000,
            txt: '订单已提交'
          }).show()
        },
        onCancel: () => {
        }
      }).show()
	}
  }
}
</script>

<style lang="scss" scoped>
@import '~SCSS/_common';
.make-order{
	font-size: 0.373333rem;
	.title{
		font-size: 0.426667rem;
		padding: 0.266667rem 0;
	}
	.memebr-address{
		padding:0.266667rem;
		background-color:#fff;
		.add{
			display: block;
			color: $mainColor;
			margin-top: 0.266667rem;
		}
	}
	.distribution-time{
		display: flex;
    	justify-content: space-between;
    	padding: 0.266667rem 0.266667rem;
    	background-color:#fff;
    	margin-top: 0.266667rem;
	}
}

.search{
		display: flex;
		background-color:#fff;
		align-items: center;
		padding: 10px;
		margin-top: 0.533333rem;
		.cube-input, button{
			height: 0.8rem;
		}
		.cube-input{
			flex:1;
			&_active{
				border: 0;
				outline:none;
			}
			&::after{
				border: 0;
			}
		}
		button{
			width: 1.333333rem;
			padding:0;
			background-color: $mainColor;
		}
}

.book-list{
	padding:0.266667rem;
	background-color: #fff;
	margin-top: 0.266667rem;
	table{
		width: 100%;
		th,td{
			text-align: center;
		}
		th{
			padding-bottom: 20px;
		}
		tbody{
			tr{
				border-bottom: 1px solid #d6d6d6;
				&:last-child{
					border-bottom:0;
				}
			}
		}
		img{
			width: 1.0rem;
			height: 1.0rem;
			border-radius: 50%;
		}
		button{
			height: 0.533333rem;
		    padding: 0;
		    width: 1.0rem;
		    font-size: 0.32rem;
		    background-color: #d6d6d6;
		    margin: 0 auto;
		    margin-left: 0.053333rem;
		}
		td{
			.text{
				display: flex;
				align-items: center;
				justify-content:center;
				min-height: 1.5rem;
			    float: left;
			    width: 100%;
			    padding: 10px 0;
			    .label{
			    	padding: 0.266667rem 0;
			    	display:flex;
			    	flex-wrap: wrap;
			    	justify-content:flex-start;
			    	max-width:5.333333rem;
			    	overflow:hidden;
			    }
			}
		}
		.cube-checkbox{
			padding:0;
		}
		.book{
			display:flex;
			flex-direction: column;
		}
	}

	.add{
		height: 0.533333rem;
	    padding: 0;
	    width: 1.0rem;
	    font-size: 0.32rem;
	    background-color: #5DBAA7;
	    margin: 0.4rem auto;
	}
}

.submit-order{
	background-color: #5DBAA7;
	margin-top: 0.8rem;
}
</style>